*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.contact{
  display: flex;
  gap:  30px;
}



nav{
  width: 100%;
  height: 80px;
  background-color: #31363b;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;
  
}

a {
  color: white;
}


.parent, .parent1 {
width: 100%;
display: flex;
/* gap: 30px;
  justify-content: space-evenly; */
justify-content: center;
margin-top: 100px;
gap: 3px;
}
.card1 {
width: 300px;
height: 500px;
text-align: center;
border: 2px solid black;
}
.card1:hover {
color: blue;
transition: 0.25s;
border: 10px solid blue;
}

img {
width: 100%;
height: 250px;
object-fit: contain;
}
.parent1{
  margin-top: 10px !important;
}
footer{
  width: 100%;
  height: 300px;
  background-color: #f5f5f7;
  margin-top: 100px;
  
}
.last a{
  color:#007bff;
  text-decoration: none;
}
.last{
  height: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;
}
